<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
div(
  :class=`[
    "c-snapchat-profile-box",
    "c-snapchat-profile-box--" + direction,
    "c-snapchat-profile-box--" + size,
    "c-snapchat-profile-box--" + theme,
    {
      "c-snapchat-profile-box--forward": !reverse,
      "c-snapchat-profile-box--reverse": reverse
    }
  ]`
)
  img(
    @click="onGoToSnapchat"
    :src="temporaryAvatar"
    class="c-snapchat-profile-box__avatar"
  )

  .c-snapchat-profile-box__information
    span.c-snapchat-profile-box__name {{ snapchatName || snapchatUsername }}

    button(
      @click="onGoToSnapchat"
      class="c-snapchat-profile-box__button"
    ) {{ buttonContent }}
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// PROJECT: DATA
import { snapchatAvatar } from "@/data/snapchat"

// PROJECT: MIXINS
import SnapchatProfileMixin from "@/mixins/widgets/SnapchatProfileMixin"

export default {
  mixins: [SnapchatProfileMixin],

  data: () => ({
    // --> STATE <--

    temporaryAvatar: snapchatAvatar
  }),

  methods: {
    // --> EVENT LISTENERS <--

    onGoToSnapchat() {
      window.open(
        `https://www.snapchat.com/add/${this.snapchatUsername}`,
        "_blank"
      )
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

<style lang="scss">
// VARIABLES
$c: ".c-snapchat-profile-box";
$sizes: "mini", "small", "default", "medium", "large";

#{$c} {
  display: flex;
  align-items: center;
  justify-content: center;

  #{$c}__avatar {
    cursor: pointer;
  }

  #{$c}__information {
    display: flex;
    align-items: center;
    flex-direction: column;

    #{$c}__name {
      font-weight: 800;
    }

    #{$c}__button {
      position: relative;
      display: inline-block;
      overflow: hidden;
      min-width: 60px;
      outline: none;
      border: 0;
      border-radius: 25px;
      background-color: #fffc00;
      color: #000000;
      vertical-align: middle;
      text-align: center;
      white-space: nowrap;
      font-weight: 800;
      transition: all 125ms linear;
      user-select: none;
      cursor: pointer;

      @include no-tap-highlight-color;

      &:hover {
        background-color: #fffd5d;
      }

      &:active {
        box-shadow: 0 1px 0 #f1af3e;
      }
    }
  }

  // --> DIRECTIONS <--

  &--horizontal,
  &--vertical {
    flex-direction: column;
  }

  // --> SIZES <--

  @each $size in $sizes {
    $i: index($sizes, $size) - 1;

    &--#{$size} {
      padding: 14px + (2px * $i);

      #{$c}__avatar {
        width: 60px + (10px * $i);
        height: 60px + (10px * $i);
      }

      #{$c}__information {
        #{$c}__name {
          margin-bottom: 11px + (1px * $i);
          font-size: 13px + (1px * $i);
          line-height: 13px + (1px * $i);
        }

        #{$c}__button {
          padding: (9px + (1px * $i)) (12px + (2px * $i));
          font-size: 11px + (1px * $i);
          line-height: 11px + (1px * $i);
          box-shadow: 0 (1px + (1px * $i)) 0 #f1af3e;

          &:active {
            transform: translateY(1px * $i);
          }
        }
      }

      &#{$c}--horizontal,
      &#{$c}--vertical {
        &#{$c}--forward {
          #{$c}__avatar {
            margin-bottom: 10px + (1px * $i);
          }
        }

        &#{$c}--reverse {
          #{$c}__avatar {
            margin-top: 10px + (1px * $i);
          }
        }
      }
    }
  }

  // --> BOOLEANS <--

  &--reverse {
    #{$c}__avatar {
      order: 1;
    }

    #{$c}__information {
      order: 0;
    }
  }
}

// --> BREAKPOINT: MOBILE WIDE <--

@include mq($from: mobileWide) {
  #{$c} {
    // --> DIRECTIONS <--

    &--horizontal {
      flex-direction: row;

      #{$c}__avatar {
        margin-top: 0 !important; // Disable margin-top set in sizes above
        margin-bottom: 0 !important; // Disable margin-bottom set in sizes above
      }
    }

    // --> SIZES <--

    @each $size in $sizes {
      $i: index($sizes, $size) - 1;

      &--#{$size} {
        &#{$c}--horizontal {
          &#{$c}--forward {
            #{$c}__avatar {
              margin-right: 10px + (1px * $i);
            }
          }

          &#{$c}--reverse {
            #{$c}__avatar {
              margin-left: 10px + (1px * $i);
            }
          }
        }
      }
    }
  }
}
</style>
